import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';


function EventDetails({current_task}) {
  return (
    <Card className="text-center">


    <div className="img">
        <Card.Img
          className="img-card"
          as="img"
          variant="top"
          src={current_task.image}
        />
        </div>

      <Card.Body>
        <Card.Title>{current_task.head}</Card.Title>
        <Card.Text>
        {current_task.fulldata}
        </Card.Text>
        <Button variant="primary" href="/join-us">Join Us</Button>
      </Card.Body>
      <Card.Footer className="text-muted"> {current_task.date}</Card.Footer>
    </Card>
  );
}

export default EventDetails;