import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Card from 'react-bootstrap/Card';
import { useForm, ValidationError } from '@formspree/react';
import { Container } from 'react-bootstrap';
import CardHeader from 'react-bootstrap/esm/CardHeader';
function Joinus() {
  const [state, handleSubmit] = useForm("xyyadbyr");
  if (state.succeeded) {
      return <p>Thanks for joining!</p>;
  }
  return (
    <Container>
    <Card border= " danger">
    <CardHeader><h2>Join-Us</h2></CardHeader>
    <Card.Body>
      <form onSubmit={handleSubmit} action="https://formspree.io/f/xyyadbyr" method="POST">


        
      <Form.Group className="mb-3" >
        <Form.Label><h5>Name</h5></Form.Label>
        <br/>
        <input
        placeholder="Name"
        id="Name"
        type="Name" 
        name="Name"
      />
      <ValidationError 
        prefix="Name" 
        field="Name"
        errors={state.errors}
      />

      </Form.Group>


      <Form.Group className="mb-3" >
        <Form.Label><h5>Email</h5></Form.Label>
        <br/>
        <input
        placeholder="Email"
        id="email"
        type="email" 
        name="email"
      />
      <ValidationError 
        prefix="Email" 
        field="email"
        errors={state.errors}
      />
      </Form.Group>

      <Form.Group className="mb-3" >
        <Form.Label><h5>Phone Number</h5></Form.Label>
        <br/>
        <input
        placeholder="Phone Number"
        id="Phone Number"
        type="Phone Number" 
        name="Phone Number"
      />
      <ValidationError 
        prefix="Phone Number" 
        field="Phone Number"
        errors={state.errors}
      />
      </Form.Group>

      

      <Button  variant="primary" disabled={state.submitting} type="submit">
        Submit Form
      </Button>


    

    </form>
    </Card.Body>
    </Card>
    </Container>

);
}

export default Joinus