import Accordion from 'react-bootstrap/Accordion';

function Events({heading ,button,summry_data}) {
  return (
    <Accordion >
      <Accordion.Item>
        <Accordion.Header>{heading}</Accordion.Header>
        <Accordion.Body>
          {summry_data}
          <h6><a href={button}>Read More...</a></h6>
        </Accordion.Body>
      </Accordion.Item>
    </Accordion>
  );
}

export default Events;