import Card from 'react-bootstrap/Card';
import Pic from "../Images/MainPic2.jpg"


function Endpic() {
  return (
    <Card className="text-center">


    <div className="img">
        <Card.Img
          className="img-card"
          as="img"
          variant="top"
          src={Pic}
        />
        </div>

      <Card.Body>
        <Card.Title> </Card.Title>
      </Card.Body>
    </Card>
  );
}

export default Endpic;