import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Pic from "../Images/MainPic.jpg"


function Bigcaard(){
return(
<Card className="text-center">


<div className="img">
    <Card.Img
      className="img-card"
      as="img"
      variant="top"
      src={Pic}
    />
    </div>

  <Card.Body>
    <Card.Title><h1>क्राईम मानीटिरिंग ब्यूरों</h1>
    
     <b>पंजीकरण सं. 213/2020</b> 

</Card.Title>
    <Card.Text>
    समाज और देश की स्थिति को देखकर ऐसा महसूस होता है कि समाज में नव-युवाओं को जगाने की जरूरत है समाज और राष्ट्र को एक मजबूत करने की आवश्यकता है हमारा लक्ष्य है स्वच्छ भारत  स्वस्थ भारत अपराध मुक्त भारत बनाने के लिए एक नया प्रयास करने के लिए देश के नव-युवाओं को उत्साहित करने के लिए क्राइम मॉनिटरिंग ब्यूरो का गठन किया गया यह गठन 17 सितंबर 2020 में भारतीय अधिनियम धारा १८८२ एक्ट के आधार पर इसका गठन किया गया है इसी के साथ समाज के उत्थान के लिए देश में अराजकता को समाप्त करने के लिए तथा समाज में हो रहे व्याप्त भ्रष्टाचार, अपराध, हिंसा, बाल उत्पीड़न ,घरेलू हिंसा, दहेज उत्पीड़न ,साइबरक्राइम ,नक्सलवाद एवं तमाम ऐसी  राष्ट विरोधी गतिविधियां जो देश और समाज के लिए घातक है,
    <br/>
    
उन सब को रोकने के लिए सरकार और प्रशासन को प्रशासन को हमारे सदस्य एवं अधिकारियो के माध्यम से सहयोग देने हेतु समय-समय पर उनको सूचनाएं मुहैया कराने हेतु पूरे देश में इस संस्था को लागू किया गया है इस संस्था का मूल उद्देश्य हिंसा और भ्रष्टाचार मुक्त भारत, 
    <br/>

    <br/>


   <b>न्यास का उददेश्य</b>
   <br/> 
   <br/>

1- साहित्य, संस्कृति कला, शिक्षा, दर्शन, तकनीकी, कृषि, चिकित्सास, समाजसेवा, युवा जागृति एंव पत्रकात्रिता आदि के क्षेत्रो में रहकर सम्पूर्ण मानवता की सेवा में संलग्न संस्थाओं, आन्दोलनों एंव व्यक्तियों को प्रोत्साहन देना तथा उनका सार्वजनिक अभिनन्दन करना व अलंकरण करना।
<br/>
<br/>
2- जन साधारण विशेष कर लक्षित समूह में समाजिक एंव आर्थिक मुद्दो पर सजगता एंव जागरुकता उत्पन्न करने के लिये समुदाय के सक्रिय समूहो एंव व्यक्तियो के माध्यम से कार्य करना महिलाओ, किशोरियों, बच्चों एंव दलित समुदाय को सम्मान जनक परिस्थितयों में जीवन यापन करने अवसरो को विकसित करना। 

<br/>
<br/>
3-समुदाय के पिछड़े गरीब एंव दलित परिवारो को आजीविका एंव भोजन सुनिश्चिता को स्थापित करने का प्रयास करना। धर्म, जाति, लिंग भेदभाव से हटकर गैर बराबरी को दूर करनेकी सक्रिय पहल करना।
    
<br/>
<br/>


4- नेकी की दुकान गरीब बच्चो को, गरीब परिवारो को अनाथालय, वृद्धा आश्रम जरुरतमंद आदि को कपड़े खाद्य सामाग्री प्रदान करना

<br/>
<br/>
5- रोटी बैंक:- माह में दो बाद जरुरतमंद को खाने की व्यवस्था कराना ।
<br/>
<br/>
6- खोया पाया :- किसी भी वस्तु का खो जाना किसी भी व्यक्ति को उसके मिल जाने पाने न्यास के काउंटर पर जमा करना ।
<br/>
<br/>
7- उड़नदस्ताः- किसी भी क्राईम पर पुलिस के साथ छापामारने में सहयोग करना ।
<br/>
<br/>
8- किसी कभी अपराध की सूचना पुलिस को व प्रशासनिक अधिकारियों या सरकारी एंजेसी आदि को सूचित करना
<br/>
<br/>
9- भ्रष्टाचार या किसी भी अपराध में लिप्त कर्मचारी को सम्बन्धित विभाग में सूचना देना ।
<br/>
<br/>


10-एगजामिनर ड्यूटी 10वी व 12वी आदि में ड्यूटी का लगाना
<br/>
<br/>
11-किसी भी एन०जी०ओ० ट्रस्ट सोसाइटी या किसी भी संस्थान के गलत पाये जाने पर उसके कार्यों पर निगरानी रखना ।
<br/>
<br/>
12- स्वच्छता अभियान- स्वच्छता के प्रति लोगो को जागरुक करना और वातावरण के प्रति जागरुक करना ।
<br/>
<br/>
13- आपदा राहत - आपदा राहत के लिये कार्य करना ।
<br/>
<br/>
14- एन०जी०ओ० ट्रस्ट या किसी भी विभाग गलत कार्य मे या अपराध मे पाया जाता है तो सम्बन्धित विभाग के अधिकारियों या प्रशासन को सूचना देने का अधिकार ।
<br/>
<br/>
15- न्यास के लिये जरुरत पड़ने पर पुलिस प्रशासन के सहयोग की मांग
    </Card.Text>
    <Button variant="primary" href="/join-us">Join Us</Button>
  </Card.Body>
</Card>
)

}

export default Bigcaard